import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service';
import {AuthService} from '../../services/auth.service';
import {FlashMessagesService} from 'ngx-flash-messages';
import {Router} from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
	name: String;
	username: String;
	email: String;
	password: String;



  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private router: Router
    ) {}

  ngOnInit() {
  }

  onRegisterSubmit(){
  	const user = {
  	name: this.name,
  	username: this.username,
  	email: this.email,
  	password: this.password
  	}


  	//Alle Felder gefüllt?

  	if (!this.validateService.validateRegister(user)){
  	this.flashMessage.show('Please fill in all fields!', {classes: ['alert-danger'], timeout: 3000});
  	return false;
  	}


  	// Validierung der Email

  	if(!this.validateService.validateEmail(user.email)){
  	this.flashMessage.show('Please enter a valid Email!', {classes: ['alert-danger'], timeout: 3000});
  	return false;
  	}


    // Register User

    this.authService.registerUser(user).subscribe(data => {
      if(data.success){
        this.flashMessage.show('You are now registered and can log in!', {classes: ['alert-success'], timeout: 3000});
        this.router.navigate(['/login']);
      } else {
        this.flashMessage.show('Something went wrong!', {classes: ['alert-danger'], timeout: 3000});
        this.router.navigate(['/register']);      

      }
    });

  }

}



